// Complex mixins.
@mixin fs-h1 {
  @include fs-h1-small;
  @include  media($medium-screen) {
    @include fs-title-1;
  }
}

@mixin fs-h1-default {
  @include fs-display-1-mobile;
  @include  media($medium-screen) {
    @include fs-display-1;
  }
}

@mixin fs-h1-home {
  @include fs-display-1-mobile;
  @include  media($medium-screen) {
    @include fs-display-1;
  }
}
/* purgecss end ignore */
